<style>
@import "../../../../assets/fileUpload.css";
</style>

<template>
  <div>
    <div class="add-form-wraper">
      <el-form ref="form" label-width="160px" @submit.native.prevent>
        <region class="region-wrap" className="add-input" :required="true"></region>
        <el-form-item label="本地上传" required>
          <div class="excel-u-d-wrap">
            <div class="u-btn-wrap">
              <div class="file-name-wrap">{{ file.name }}</div>
              <div class="label-wrap">
                <label for="excelFile" class="excel-file-label">
                  <input type="file" id="excelFile" hidden @change="handleFileSelect"/>
                  <span class="u-txt">选择文件</span>
                </label>
              </div>
            </div>
            <div class="d-btn-wrap">
              <span>请按EXCEL模板格式导入</span>
              <el-button type="text" @click="downloadModel" size="mini" :disabled="isDisabled">下载模板</el-button>
            </div>
          </div>
        </el-form-item>

        <el-form-item label="批量更新门店售价" required>
          <div style="width: 740px;"> 
            <region-store :regionCode="regionCode" :platformId="platformId" @setCheckedData="setCheckedData"></region-store>
          </div>
        </el-form-item>

        <hr class="s-slip-tb">
        <el-form-item>
          <el-button type="primary" size="small" @click="doSaveHandel" :disabled="isDisabled">保存</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import exportSheet from '@/utils/exportSheet'
  import RegionStore from '@/components/Common/StoreCommon/RegionStore'
  import Region from '@/components/Common/StoreCommon/Region'
  const salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
  const reqUrls = {
    // 预售商品列表
    searchTableData: '/plateform/a/platformCityGoods/presale/platformCityGoodsList',
    // excel及时达区域门店调价
    excelUpdateCityStorePrice: '/plateform/a/product/store/presale/excelUpdateCityStorePrice'
  }
  export default {
    data() {
      return {
        isDisabled: false,
        file: {name: ''}, // 导入数据
        // 选中的list
        checkedList: [],
        // 售卖类型：1：常规；2：预售；3:长周期预售，4：及时达
        saleType: 4 ,
        platformId: 5,
        tempConfig: {
          platformName: '平台(禁止修改)',
          platformCityGoodsId: '商品ID(禁止修改)',
          goodsNo: 'SKU编码(禁止修改)',
          skuNo: 'ERP编码(禁止修改)',
          goodsName: '商品名称(禁止修改)',
          spec: '商品规格(禁止修改)',
          categoryName: '所属分类(禁止修改)',
          regionCode: '区域ID(禁止修改)',
          regionName: '区域名称(禁止修改)',
          newPrice: '新售价即要调整的售价(必填)'
        },

        adjustPriceList: [], // 调价集合
        regionCode: Storage.get("regionCode")
      }
    },
    components: {
      "region-store": RegionStore,
      "region": Region
    },
    methods: {
      /**
       * [downloadModel 下载模板]
       * @return {[type]} [description]
       */
      downloadModel () {
        this.isDisabled = true
        this.$http.post(reqUrls.searchTableData, {platformId: this.platformId, saleType: this.saleType, regionCode: this.regionCode, DIFFCITY: true}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            if(res.data && res.data.length) {
              let tableDataArr = res.data
              exportSheet.exportFileByConfig(tableDataArr, '及时达批量调门店售价模板.xlsx', this.tempConfig)
              this.$notify.success({
                title: '温馨提示',
                message: '及时达批量调门店售价模板下载成功'
              })
            }else {
              this.$notify.warning({
                title: '温馨提示',
                message: '未获取到数据，下载失败'
              })
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = true
          this.$notify.error({
            title: '提示(下载模板)：',
            message: e.message
          })
        })
      },
      /**
       * [handleFileSelect 选择文件发生变化]
       * @param  {[type]} e [description]
       * @return {[type]}   [description]
       */
      handleFileSelect (e) {
        // 点击取消不做任何变化
        if (!e.target.files.length) {
          return
        }
        this.file = e.target.files[0] || {name: ''}
        let idx = this.file.name.lastIndexOf(".")
        let strLength = this.file.name.length
        let postf = this.file.name.substring(idx, strLength) // 后缀名

        if (!this.file.name || postf != '.xlsx') {
          this.$notify.warning({
            title: '提示',
            message: '请选择excel文件'
          })
          // 清除当前选择的文件信息
          this.file = {name: ''}
          document.getElementById('excelFile').value = ""
          return
        }

        let items = []
        let parseConfig = {}
        for (let key in this.tempConfig) {
          parseConfig[this.tempConfig[key]] = key
        }
        items = exportSheet.parseToJSON(this.file, parseConfig)
        items.then((result) => {
          // 过滤空行
          let resultVal = result.filter(item => !!item.platformCityGoodsId)
          let adjustPriceListArr = []
          for (let item of resultVal) {
            if(!item.newPrice) {
              this.$notify.warning({
                title: '提示',
                message: `请填写(${item.platformCityGoodsId}_${item.goodsName})的新售价`
              })
              // 清除当前选择的文件信息
              this.file = {name: ''}
              document.getElementById('excelFile').value = ""
              return
            }else {
              if(!salePriceReg.test(item.newPrice)) {
                this.$notify.warning({
                  title: '提示',
                  message: `(${item.platformCityGoodsId}_${item.goodsName})的新售价为大于0，小数保留小数点后2位`
                })
                // 清除当前选择的文件信息
                this.file = {name: ''}
                document.getElementById('excelFile').value = ""
                return
              }
            }

            // 及时达
            let adjustPriceListItem = {
              platformCityGoodsId: item.platformCityGoodsId,
              price: item.newPrice,
              goodsNo: item.goodsNo
            }
            adjustPriceListArr.push(adjustPriceListItem)
          }
          
          this.adjustPriceList = adjustPriceListArr
        }).catch()
      },
      /**
       * [doSaveHandel 保存操作]
       * @return {[type]} [description]
       */
      doSaveHandel() {
        if(this.adjustPriceList.length === 0) {
          this.$message.warning("请先上传商品")
          return
        }
        if(this.checkedList.length === 0) {
          this.$message.warning("请选择批量更新门店")
          return
        }
        let storeNos = this.checkedList.map(item => {
          return item.storeNo
        })

        this.isDisabled = true
        this.saveLoading = true
        this.$http.post(reqUrls.excelUpdateCityStorePrice, {adjustPriceList: this.adjustPriceList, storeNos: storeNos, DIFFCITY: true}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
            this.saveLoading = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("批量调门店售价成功")
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.saveLoading = false
          this.$notify.error({
            title: '提示',
            message: e.message,
            duration: 0
          })
        })
      },
      /**
       * @description: 设置选择门店的值
       * @param {type} 
       * @return: 
       */
      setCheckedData(checkedList) {
        this.checkedList = checkedList || []
      }
    }
  }
</script>


